<script>
	export default {
		name: "MyItem",
		/* functional:true  props  state*/
		/* https://cn.vuejs.org/v2/guide/render-function.html#%E5%87%BD%E6%95%B0%E5%BC%8F%E7%BB%84%E4%BB%B6 */
		functional: true,
		props: {
			icon: {
				type: String,
				default: '',
			},
			title: {
				type: String,
				default: '',
			}
		},
		render(h, context) {
			const {icon, title} = context.props;
			const vnodes = [];
			if (icon) {
				vnodes.push(<my-svg-icon icon={icon}></my-svg-icon>);
			}
			if (title) {
				vnodes.push(<span slot='title'>{title}</span>);
			}
			return vnodes;
		}
	}
</script>
